.footer{
    max-width: 479px;
}
.footer__container{
     padding-top: 0;
    padding-bottom: 0;
}
.footer__contacts{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.footer__logo{
        // display: flex;
    // justify-content: center;
    padding-top: 30px;
}
.footer__insta {
    display: block;
    margin: 0 auto; 
    width: 40px;
    height: 40px;
    
}
.footer__copy {
    min-width: 320px;
    height: 70px;
    background: #111518;
    
}
.copy__text {
    
    padding-left: 52px;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 70px;
    color: #B9B9B9;
}
.footer__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    list-style-type: none;
}
.footer__item:not(:last-child){
    margin-top: 20px;
    margin-bottom: 40px;
}
.footer__tel{
    display: flex;
    align-items: center;
    text-decoration: none;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.21;
    letter-spacing: 0.08em;
    color: #111518;
}
.footer__tel-icon {
    width: 20px;
    height: 20px;
    margin-right: 10px;
}
@media screen and (min-width: 480px) {
    .footer{
        max-width: 767px;
    }
}
@media screen and (min-width: 768px){
    .footer{
        max-width: 1365px;
    }
    .footer__logo{
        padding-top: 0;
    }
    .footer__contacts{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        // margin-left: 40px;
        // margin-right: 40px;
    }
    .footer__list{
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin-bottom: 40px;
        margin-top: 40px;
    }
    .footer__item:not(:last-child){
        margin-top: 0;
        margin-bottom: 0;
    }
    .footer__tel{
        font-size: 20px;
    }
    .footer__tel-icon {
        width: 24px;
        height: 24px;
        
    }
    .footer__tel::after {
        content: "";
        display: inline-block;
        width: 0px;
        height: 40px;
        border: 1px solid #B9B9B9;
        margin-left: 24px;
        margin-right: 24px;
    }
    .copy__text{
        padding-left: 40px;
    }
}
@media screen and (min-width: 1366px){
    .footer{
        //  max-width: 1680px;
        //  width: 100%;
        //  min-width: 1366px;
        max-width: none;
    }
    .footer__contacts{
       
        height: 200px; 
       margin-left: 0;
             
    }
    .footer__logo{
        margin-bottom: 53px;
    }
    .footer__list{
        margin: 125px auto 35px auto;
        justify-content: center;
        align-items: flex-end;
    }
    .footer__tel{
        font-size: 30px;
    }
    .footer__tel-icon {
        width: 30px;
        height: 30px;
        
    }
    .footer__copy{
        margin-bottom: 56px;
    }
    .footer__logo{
        margin-top: 100px;
        padding-top: 0;
        padding-left: 183px;
    }
    .copy__text{
        padding-left: 183px;
    }
}